import React from 'react'
import SideNav from '../components/side-nav'
import image1 from '../assets/1.png'
import image2 from '../assets/2.png'

const Home = () => {
  return (
    <div className='home'>
      <SideNav />
      <div className='container'>
        <div className='heading'>
          <div className='f-image'>
            <div className='wrapper'>
              <div className='image'>
                <img src={image1} alt='Jr fancy ui' />
              </div>
              <div className='image second'>
                <img src={image2} alt='Jr fancy ui' />
              </div>
            </div>
          </div>
          <h1>Jr Fancy UI | A modern React UI library</h1>
        </div>
        <div className='comp-section' id='layouts'></div>
      </div>
    </div>
  )
}

export default Home
